@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Stapel";
  font-display: swap;
  src: url("../fonts/stapel-bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/raleway-semibold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-regular.woff2") format("woff2");
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', "Arial", sans-serif;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.site-list {
  font-size: 24px;
  line-height: 28px;
  margin: 80px 0 0 0 ;
  padding: 0;
}
.site-list__item {
  list-style: none;
  padding: 22px 0 0 40px;
}
.site-list__link {
  color: #000000;
  text-decoration: none;
  display: block;
}
.main-nav {
  margin: 25px 0 90px 0;
  width: 375px;
  position: relative;
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--nojs .site-list {
    display: block;
  }
  .main-nav__toggle {
    position: absolute;
    right: 15px;
    top: -20px;
    z-index: 2;
    display: block;
    width: 40px;
    height: 40px;
    background-color: #ffffffff;
    border: none;
    cursor: pointer;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 7px;
    width: 36px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .main-nav--opened .main-nav__toggle {
    top: 5px;
    right: 0;
    background-color: #ffffffff;
  }
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: -10px;
    left: 0;
    width: 23px;
    height: 22px;
    background: url("../img/icon-cross.svg") center no-repeat;
  }
}
.logo {
  margin-left: 12px;
}
.page-main {
  width: 375px;
  text-align: center;
}
.page-main__title {
  font-family: "Stapel";
  font-size: 38px;
  text-transform: uppercase;
  line-height: 48px;
  margin: 0 0 20px 12px;
  text-align: left;
}
.page-main__text {
  font-size: 20px;
  margin: 0 0 0 12px;
  text-align: left;
}
.page-main__background {
  background: url("../img/main-background.jpg") no-repeat center;
  height: 430px;
  width: 375px;
}
.modal-button {
  padding: 12px 20px 12px 20px;
  font-size: 16px;
  background-color: #fff;
  border: 1px solid #0D0D0D;
  font-weight: 600;
  width: 200px;
  margin: auto;
}
.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  background-color: #ffffffff;
  padding: 25px 0 0 0;
  width: 375px;
  min-height: 667px;
}
.modal__close {
  position: absolute;
  top: 15px;
  right: 20px;
  border: 0;
  width: 36px;
  height: 36px;
  background: url("../img/icon-cross.svg") center no-repeat;
}
.modal__title {
  font-family: Stapel;
  font-size: 38px;
  font-weight: 700;
  position: absolute;
  margin: 0;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
